body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/*$font-choice-button: 'Bree Serif', serif;*/
* {
  margin: 0; }

html, body {
  height: 100%; }

a {
  text-decoration: none;
  color: inherit; }

#root {
  height: 100%; }

.Screen {
  margin: 0 auto;
  width: 560px;
  height: 900px;
  border: 1px solid #000000;
  background-color: #FFFFFF;
  background: linear-gradient(to bottom, white 0%, white 70%, #dbdbdb 100%);
  display: grid;
  grid-template-rows: 1fr;
  grid-template-columns: 1fr; }

@media all and (max-width: 1000px) and (min-height: 320px) {
  .Screen {
    width: 100%;
    height: 100%; } }

.App {
  text-align: center;
  background-color: #333333;
  height: 100%;
  font-family: "Montserrat", sans-serif; }

h1, h2, h3, h4, h5, h6 {
  font-family: "Montserrat", sans-serif; }

.FaultReport {
  display: grid;
  grid-template-rows: 20% 10% auto; }

.FaultReport img {
  width: 50%;
  margin: auto; }

.FaultReportTitle {
  color: #682C91; }

.FaultReportButton {
  width: 30%;
  /*height: 50px;*/ }

.FaultReportText textarea {
  font-size: 15px; }

.FaultReportHeader {
  font-size: 30px; }

.FaultReportQuestions {
  display: grid;
  grid-template-rows: 20% 1fr;
  -webkit-align-items: end;
          align-items: end; }

.FaultReportQuestionsChoices {
  /*height:30%;*/
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 10px;
  margin: 10px;
  font-family: "Montserrat", sans-serif; }

.FaultReportQuestionsItem {
  background-color: #FFFFFF;
  border: 6px solid #04a52a;
  padding: 20px 0px;
  font-size: 30px;
  padding: 50px 20px;
  border-radius: 10px;
  cursor: pointer; }

.FaultReportQuestionsItem div:hover {
  color: #AAAAAA; }

.FaultReportQuestionsItem:active {
  color: #AAAAAA;
  -webkit-animation: fadeinout 4s linear forwards;
  animation: fadeinout 4s linear forwards; }

.FaultReportQuestionsQuestion {
  font-size: 30px; }

.FaultReportQuestionsItemText {
  font-size: 40px; }

.FaultReportResult {
  font-size: 20px;
  margin: 0 auto;
  overflow: auto; }

.FaultReportResultInput {
  margin: auto;
  width: 380px;
  text-align: left;
  font-size: 16px; }

.FaultReportResultInput textarea {
  resize: none;
  font-size: 16px; }

.FaultReportResultInput button {
  padding: 14px;
  font-size: 20px; }

.FaultReportSuggestion {
  display: grid;
  grid-template-rows: 20% auto;
  -webkit-align-items: end;
          align-items: end;
  font-size: 30px; }

.FaultReportSuggestionChoices {
  margin: 10px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 10px;
  font-family: "Montserrat", sans-serif; }

.FaultReportSuggestionItem {
  font-size: 20px;
  padding: 50px 0px;
  background-color: #FFFFFF;
  border-radius: 10px;
  border: 6px solid #04a52a;
  cursor: pointer; }

.NegChoice {
  background-color: #FFFFFF;
  border: 6px solid #f21d1d; }

.PosChoice {
  background-color: #FFFFFF; }

.FaultReportSolutions {
  overflow: auto; }

::-webkit-scrollbar {
  width: 0px;
  /* Remove scrollbar space */
  background: transparent;
  /* Optional: just make scrollbar invisible */ }

.FaultReportSolutionChoices {
  margin: 10px 50px;
  display: grid;
  grid-gap: 10px;
  -webkit-align-items: end;
          align-items: end;
  font-family: "Montserrat", sans-serif; }

.FaultReportSolutionItem:first-child .FaultReportSolutionItemText {
  display: block; }

.FaultReportSolutionItem:first-child {
  padding: 0px;
  border: 0px solid #04a52a;
  border-radius: 10px;
  outline: none;
  cursor: default; }

.FaultReportSolutionItem {
  padding: 10px;
  border: 1px solid #04a52a;
  border-radius: 10px;
  outline: none;
  cursor: pointer; }

.FaultReportSolutionItemHeader {
  font-size: 18px; }

.FaultReportSolutionItemText a {
  color: blue; }

.FaultReportSolutionItem:focus-within .FaultReportSolutionItemText {
  display: block; }

.FaultReportSolutionItemText {
  text-align: left;
  padding: 20px;
  display: none; }

@-webkit-keyframes fadeinout {
  100% {
    opacity: 1; }
  50% {
    opacity: 0.5; }
  0% {
    opacity: 0; } }

@keyframes fadeinout {
  100% {
    opacity: 1; }
  50% {
    opacity: 0.5; }
  0% {
    opacity: 0; } }

