$color-background: #FFFFFF;
$color-nejbor-purple: #682C91;
$color-titles: $color-nejbor-purple;
$color-text: #000000;
$color-choice-button-border: #04a52a;//#41E9C1;
$color-choice-button-border-neg: #f21d1d;//#FE5065;
$color-choice-button: #FFFFFF;
$color-choice-button-neg: #FFFFFF;
$color-choice-button-text-hover: #AAAAAA;
$color-app-background: #333333;
$color-app-border: #000000;

$font-main: 'Montserrat', sans-serif;
$font-choice-button: $font-main;
/*$font-choice-button: 'Bree Serif', serif;*/
$border-choice-size: 6px;

* {
    margin: 0;
}

html, body {
    height: 100%;
}

a {
    text-decoration:none;
    color: inherit;
}

#root {
    height:100%;
}

.Screen {
    margin: 0 auto;
    width: 560px;
    height: 900px;
    border: 1px solid $color-app-border;
    background-color: $color-background;
    background: linear-gradient(to bottom, rgba(255,255,255,1) 0%,rgba(255,255,255,1) 70%,rgba(219,219,219,1) 100%);
    display:grid;
    grid-template-rows: 1fr;
    grid-template-columns: 1fr;
}

@media all and (max-width: 1000px) and (min-height: 320px) {
    .Screen {
        width: 100%;
        height: 100%;
    }
}

.App {
    text-align: center;
    background-color: $color-app-background;
    height:100%;
    font-family: $font-main;
}

h1, h2, h3, h4, h5, h6 {
    font-family: $font-main;
}
.FaultReport {
    display: grid;
    grid-template-rows:20% 10% auto;
}

.FaultReport img {
    width:50%;
    margin: auto;
}

.FaultReportTitle {
    color: $color-titles;
}

.FaultReportButton {
    width: 30%;
    /*height: 50px;*/
}

.FaultReportText textarea {
    font-size:15px;
}

.FaultReportHeader {
    font-size: 30px;
}

.FaultReportQuestions {
    display:grid;
    grid-template-rows: 20% 1fr;
    align-items: end;
}

.FaultReportQuestionsChoices {
    /*height:30%;*/
    display:grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 10px;
    margin: 10px;
    font-family: $font-choice-button;
}

.FaultReportQuestionsItem {
    background-color: $color-choice-button;
    border: $border-choice-size solid $color-choice-button-border;
    padding: 20px 0px;
    font-size:30px;
    padding:50px 20px;
    border-radius: 10px;
    cursor: pointer;
}

.FaultReportQuestionsItem div:hover {
    color: $color-choice-button-text-hover;
}

.FaultReportQuestionsItem:active {
    color: $color-choice-button-text-hover;
    -webkit-animation: fadeinout 4s linear forwards;
    animation: fadeinout 4s linear forwards;
}

.FaultReportQuestionsQuestion {
    font-size:30px;
}
.FaultReportQuestionsItemText {
    font-size:40px;
}

.FaultReportResult {
    font-size:20px;
    margin:0 auto;
    overflow:auto;
}

.FaultReportResultInput {
    margin: auto;
    width: 380px;
    text-align: left;
    font-size:16px;
}

.FaultReportResultInput textarea {
    resize: none;
    font-size:16px;
}

.FaultReportResultInput button {
    padding: 14px;
    font-size:20px;
}

.FaultReportSuggestion {
    display: grid;
    grid-template-rows: 20% auto;
    align-items: end;
    font-size:30px;
}

.FaultReportSuggestionChoices {
    margin: 10px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 10px;
    font-family: $font-choice-button;
}

.FaultReportSuggestionItem {
    font-size:20px;
    padding: 50px 0px;
    background-color: $color-choice-button;
    border-radius: 10px;
    border: $border-choice-size solid $color-choice-button-border;
    cursor: pointer;
}

.NegChoice {
    background-color: $color-choice-button-neg;
    border: $border-choice-size solid $color-choice-button-border-neg;
}

.PosChoice {
    background-color: $color-choice-button;
}

.FaultReportSolutions {
    overflow: auto;
}

::-webkit-scrollbar {
    width: 0px;  /* Remove scrollbar space */
    background: transparent;  /* Optional: just make scrollbar invisible */
}

.FaultReportSolutionChoices {
    margin: 10px 50px;
    display:grid;
    grid-gap: 10px;
    align-items: end;
    font-family: $font-choice-button;
}

.FaultReportSolutionItem:first-child .FaultReportSolutionItemText {
    display: block;
}

.FaultReportSolutionItem:first-child {
    padding:0px;
    border:0px solid $color-choice-button-border;
    border-radius: 10px;
    outline:none;
    cursor:default;
}
.FaultReportSolutionItem {
    padding:10px;
    border:1px solid $color-choice-button-border;
    border-radius: 10px;
    outline:none;
    cursor:pointer;
}

.FaultReportSolutionItemHeader {
    font-size: 18px;
}

.FaultReportSolutionItemText a {
    color: blue;
}

.FaultReportSolutionItem:focus-within .FaultReportSolutionItemText {
    display: block;
}

.FaultReportSolutionItemText {
    text-align:left;
    padding: 20px;
    display: none;
}


@-webkit-keyframes fadeinout {
  100% { opacity: 1; }
  50% { opacity: 0.5; }
  0% { opacity: 0; }
}

@keyframes fadeinout {
  100% { opacity: 1; }
  50% { opacity: 0.5; }
  0% { opacity: 0; }
}
